<template>
  <div class="detail-page">
    <van-nav-bar
  title="面经详情"
  left-text="返回"
  color="#fff"
  fixed
  @click-left="$router.back()"/>
  <header>
  <h1>{{ article.stem }}</h1>
  <p>{{ article.createdAt }} | {{ article.views }} 浏览量 | {{article.views}} 点赞数</p>
  <div class="userInfo" ><img :src="article.avatar" alt=""> <div>{{ article.creator }}</div> </div>
  </header>
  <main class="body" v-html="article.content"></main>
  <div class="opt">
    <van-icon name="like-o" @click="toggleLike" :class="{active:article.likeFlag}"/>
    <van-icon name="star-o" @click="toggleCollect" :class="{active:article.collectFlag}"/>
  </div>
  </div>
</template>

<script>
import { getDetail, toggleInterView } from '@/api/article'
export default {
  name: 'DetailPage',
  data () {
    return {
      article: ''
    }
  },
  async created () {
    const res = await getDetail(this.$route.params.id)
    this.article = res.data.data
  },
  methods: {
    async toggleLike () {
      await toggleInterView({ id: this.article.id, type: 1 })
      this.article.likeFlag = !this.article.likeFlag

      if (this.article.likeFlag) {
        this.$toast.success('点赞成功')
        this.article.likeCount++
      } else {
        this.$toast.success('取消点赞')
        this.article.likeCount--
      }
    },
    async toggleCollect () {
      await toggleInterView({ id: this.article.id, type: 2 })
      this.article.collectFlag = !this.article.collectFlag

      if (this.article.collectFlag) {
        this.$toast.success('收藏成功')
      } else {
        this.$toast.success('取消收藏')
      }
    }
  }
}
</script>

<style  lang="less">
  .van-nav-bar .van-nav-bar__text{
    color: #fa6d1d;
  }
  .detail-page{
    margin-top: 50px;
    padding: 0 15px;
     h1{
      font-size: 24px;
    }
    p{
      color: #999;
      font-size: 14px;
    }
    .userInfo{
      display: flex;
      align-items: center;
      img{
        width: 40px;
        height: 40;
        border-radius: 50%;
      }
      div{
        margin-left: 3px;
        font-size: 12px;
      }
    }
  }
  .opt {
    position: fixed;
    bottom: 100px;
    right: 0;
     .van-icon {
      margin-right: 20px;
      background: #fff;
      width: 40px;
      height: 40px;
      line-height: 40px;
      text-align: center;
      border-radius: 50%;
      box-shadow: 2px 2px 10px #ccc;
      font-size: 18px;
      &.active {
        background: #FEC635;
        color: #fff;
      }
    }
  }
</style>
